*{
margin:0;
padding:0;
}
.form-box{
width:50%;
height:100%;	
background:#fff;
position:relative;
overflow:hidden;
}

.illustration-box{
width:50%;
height:100%;
background:#fff;
top:25px;
justify-content:center;
display:block;
overflow:hidden;
}
.illustration-box img{
display:block;
margin-left:-80px;
width:100%;
margin-top:40px;
}
.button-box{

}
.mainContainer{
width:90vw;
height:90vh;
position:relative;
margin:auto auto;
background:#fff;
top:25px;
display:flex;
}
.headerText{
position:relative;	
height:20%;
margin-top:48px;
padding:0px;
margin-bottom:0px;
margin-left:24px;
}
.smallText{
font-size:16px;
user-select:none;
font-family:Roboto;
margin: auto auto;
padding:4px;
}
.signinButton{
background-color:#014F86;
color:#fff;
font-size:18px;
font-family:Roboto;
padding-top:8px;
padding-bottom:8px;
width:100%;
border:0;
cursor:pointer;
}


.headingTextContainer{
display:flex;
position:relative;
top:32px;
width:100%;
flex-direction:column;
}
.headingTextItem{
padding:0px;
display:block;
font-family:Roboto;
user-select:none;
margin-left:16px;
}
.headingText{
color:#000;
font-size:32px;
margin-bottom:8px;
padding:0;
}
.regularText{
color:#121212;
font-size:24px;
}
.formContainer{
display:flex;
flex-direction:column;
width:60%;
position:relative;
margin-left:16px;
margin-top:54px;
}
.formItem{
display:block;
margin-top:16px;
margin-bottom:16px;
}
.getStarted{
color:#2C7DA0;
border:none;
text-decoration:none;
}
@media only screen and (max-width:1024px)
{
.illustration-box{
display:none;
}
.form-box{
width:100%;
}
.formContainer{
display:flex;
flex-direction:column;
width:80%;
position:relative;
margin-left:10%;
margin-top:54px;
}
}